import React from "react";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import Img from 'gatsby-image';
import {Link} from 'gatsby';
import {createUseStyles} from 'react-jss';
import {firstNCharts, mdToTxt } from '../util';
import Moment from "react-moment";

const useStyles = createUseStyles({

    articleEntryLink:{
        color: 'unset',
        textDecoration: 'none',
        '&:hover':{
            color: 'unset',
            textDecoration: 'none'
        }
    },

    articleEntryCard:{
        borderRadius: '4px',
        backgroundColor: '#fff',
        boxShadow: ' 0 6px 10px rgba(0,0,0,.08), 0 0 6px rgba(0,0,0,.05)',
        transition: '.3s transform cubic-bezier(.155,1.105,.295,1.12),.3s box-shadow,.3s -webkit-transform cubic-bezier(.155,1.105,.295,1.12)',
        cursor: 'pointer',
        '&:hover':{
            transform: 'scale(1.05)',
            boxShadow: '0 10px 20px rgba(0,0,0,.12), 0 4px 8px rgba(0,0,0,.06)'
        }
    }
});



export default ({article})=>{
    const classes = useStyles();
    return (
        <Card border="secondary" className={['mb-5', classes.articleEntryCard]}>
            <Link to={`/article/${article.node.slug}`} className={classes.articleEntryLink}>
                <Card.Img 
                    as={Img}  
                    fluid={article.node.image.childImageSharp.fluid}
                    style={{height:'200px', textAlign:'center'}}
                    imgStyle={{ position: "static", width:'auto', height:'100%'}} 
                    />

                <Card.Body>
                   <span style={{color:'gray',fontSize:'smaller'}}>[ {article.node.category.name} ]</span> 
                    <Card.Title>
                    {article.node.title}
                    </Card.Title>

                    <Card.Text>
                        { firstNCharts(mdToTxt(article.node.content),25) }
                    </Card.Text>
                </Card.Body>
            </Link>
            <Card.Footer>
                <Row style={{fontSize:'small'}}>
                    {/* author icon and name
                    <Col className="d-flex align-items-center">
                        {article.node.author.picture && (
                            <Img
                            fixed={article.node.author.picture.childImageSharp.fixed}
                            imgStyle={{ position: "static", borderRadius: "50%" }}
                            />
                        )}
                        <div className="ml-1">{article.node.author.name}</div>
                    </Col>
                    */}
                     { article.node.tags && article.node.tags.length>0 && 
                        <Col>
                            <div style={{fontSize:'small'}}>
                            標籤:
                            {
                                article.node.tags.map(tag => {
                                    return (
                                        <Link to={`/tag/${tag.slug}`}  className="px-1" key={tag.tagName}>
                                            {`#${tag.tagName}`}
                                        </Link>
                                    )
                                })
                            }
                            </div>
                        </Col>
                    }
                    <Col className="d-flex align-items-center justify-content-end">
                        <span>發佈於  <Moment format="YYYY/MM/DD HH:mm:ss" date={article.node.publishedAt}/></span>
                    </Col>
                </Row>
            </Card.Footer>
        </Card>
    );
};